/* ==========================================================================
0.Navbar
========================================================================== */
nav.dashboard-nav {
    margin-left: 80px;
    position: fixed;
    width: 100%;
    right: 0;
    left: 0;
    max-width: calc(100% - 80px);
    border-bottom: 1px solid $fade-grey;
    transition: all .5s;

    .container {
        .navbar-brand {
            margin-left: 0;
        }

        .navbar-menu {
            margin-right: 0;
        }
    }

    .container.is-fluid {
        margin: 0;
        height: 65px;
    }

    //logout button
    .logout-button {
        width: 65px;
        cursor: pointer;
        display: flex;
        justify-content: center;
        align-items: center;

        i {
            color: $title-grey !important;
            margin-left: 0 !important;
            top: 0 !important;
        }

        &:hover {
            background: $light-grey;

            i {
                color: $secondary !important;
            }
        }
    }

    //icon menu item
    .nav-icon {
        width: 65px;
        height: 65px;
        display: flex;
        justify-content: center !important;
        align-items: center;

        i {
            font-size: 18px;
            font-weight: normal;
            color: $title-grey;
            position: relative;
            cursor: pointer;
        }

        &:hover i {
            color: $secondary;
        }
    }

    .navbar-item {
        //reader mode switch
        &.reader-switch .field {
            padding-top: 7px;
        }

         //new messages and notifications indicator
         .new-circle {
            width: 13px;
            height: 13px;
            left: 9px;
            top: -8px;
            position: absolute;
            border-radius: 50px;
            border: 2px solid white;
            background-color: $primary;
        }
    }

    //QuickView
    .chat-button {
        width: 65px;
        background-color: $secondary;
        cursor: pointer;
        transition: all 0.3s;
        display: flex;
        justify-content: center;
        align-items: center;

        i {
            color: $white !important;
            font-weight: 900 !important;
        }

        &:hover {
            opacity: 0.9;
        }
    }
}

//Pushed navbar state when sidebar is open
.is-pushed {
    transform: translateX(250px);
}

